import { Component, OnInit } from '@angular/core';
import { UserService } from '../shared/user.service';
import { BookService } from '../shared/book.service';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { RecBooks } from '../shared/rec-books.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private router: Router, private userService: UserService, private bookService: BookService) { }

  emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  showSuccessMessage: boolean;
  serverErrorMessage: string;
  mobile: number;
  ServerErrorMessage: string;

  ngOnInit() {
    this.getRecBooks();
  }

  getRecBooks() {
    this.bookService.getRecBooks().subscribe(res => {
      this.bookService.recBooks = res as [];
    },
      err => {
        console.log(err);
      })
  }

  shop() {
    this.router.navigateByUrl('/shop');
  }

  navigate() {
    this.router.navigateByUrl('/login');
  }

  // Form Reset
  resetForm(form?: NgForm) {
    if (form)
      form.reset();
  }

  //Sign Up Form Submit
  onSubmitSignUp(form: NgForm) {
    this.userService.SignUpUser(form.value).subscribe(
      res => {
        this.showSuccessMessage = true; //Now success message is displayed
        setTimeout(() => { this.showSuccessMessage = false; }, 4000);
        this.resetForm(form);           //resetting the form values after signup
        this.router.navigateByUrl('/login');
      },
      err => {
        if (err.status === 422)
          this.serverErrorMessage = "Email ID already registered";
        else
          this.serverErrorMessage = 'Something went wrong!Please contact the admin. ';
      })
  }
  //Login Form Submit
  OnSubmitLogin(form: NgForm) {
    this.userService.LoginUser(form.value).subscribe(
      res => {
        this.resetForm(form);
        this.userService.setToken(res['token']);
        this.userService.getUserProfile().subscribe(
          res => {
            this.userService.userDetails = res['user'];
          },
          err => {
            console.log("Error retrieving user" + JSON.stringify(err, undefined, 2));
          }
        );
        this.router.navigateByUrl('/home');
      },
      err => {
        if (err.status === 401)
          this.ServerErrorMessage = err.error;
      });
  }
}



