import { Component, OnInit } from '@angular/core';
import { UserService } from '../shared/user.service';
import { BookService } from '../shared/book.service';
import { Order } from '../shared/order.model';
import { NavigationEnd, Router } from '@angular/router';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})

export class OrdersComponent implements OnInit {

  constructor(private userService: UserService, private bookService: BookService, private router: Router) { }

  ngOnInit() {
    this.GetOrders();
  }

  GetOrders() {
    this.bookService.getOrders().subscribe(res => {
      this.bookService.orders = res as [];
    },
      err => {
        console.log('Error Retrieving Orders' + JSON.stringify(err, undefined, 2));
      });
  }

  DeleteOrders(ID: string) {
    this.bookService.deleteOrders(ID).subscribe(res => {
      this.GetOrders();
    },
      err => {
        console.log('Error in deleting orders' + JSON.stringify(err, undefined, 2));
      })
  }
}


