import { Component, OnInit } from '@angular/core';
import { BookService } from "../shared/book.service";
import { UserService } from '../shared/user.service';
import { Book } from '../shared/book.model';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})


export class CartComponent implements OnInit {

  constructor(private bookService: BookService, private userService: UserService, private http: HttpClient) { }

  cartItems: any = [];
  totalPrice: Number = 0;

  ngOnInit() {
    this.GetCartItems();
  }

  GetCartItems() {

    this.bookService.getCartItems().subscribe((res) => {

      this.bookService.cartBooks = res as [];

      this.bookService.cartBooks.forEach(cartItem => {
        this.totalPrice = Number(this.totalPrice) + (cartItem['bookId']['bprice'] * cartItem['quantity']);
      })
    },
      err => {
        console.log('Error' + JSON.stringify(err, undefined, 2));
      })
  }

  DeleteCartItem(id: string) {
    this.bookService.deleteCartItem(id).subscribe(res => {
      this.GetCartItems();
    })
  }

  increaseQuan(book) {
    book.quantity = book.quantity + 1;
    this.totalPrice = Number(this.totalPrice) + book.bookId.bprice;
  }

  decreaseQuan(book) {
    if (book.quantity > 1) {
      book.quantity = book.quantity - 1;
      this.totalPrice = Number(this.totalPrice) - book.bookId.bprice;
    }
  }

  placeOrder() {

    const paymentData = {
      purpose: 'Order Payment',
      amount: this.totalPrice,
      buyer_name: 'Mister Bookman',
      email: this.userService.userDetails.email,
      phone: this.userService.userDetails.mobile,
      user_id: this.userService.userDetails._id,
      redirect_url: `https://bookmanserver.herokuapp.com/pay/redirect?user_id=${this.userService.userDetails._id}`,
      webhook_url: '/webhook/'
    };

 

    this.http.post('https://bookmanserver.herokuapp.com/payAmount', paymentData).subscribe(response => {

      //Current Order
      this.bookService.cartBooks.forEach(cartItem => {
        this.bookService.currentOrder.books.unshift({ bookId: cartItem['bookId']['_id'], quantity: cartItem['quantity'] })
      })

      this.bookService.OrderBook(this.bookService.currentOrder).subscribe(res => {
        this.bookService.cartBooks.forEach(cartItem => {
          this.DeleteCartItem(cartItem['bookId']['_id'])
        });

        console.log('Order Confirmed');
      },
        err => {
          console.log('Error in placing order' + JSON.stringify(err, undefined, 2));
        });

      window.location.href = response as string; //setting location to redirect_url window
    },
      error => {
        console.log('Error in payment request' + JSON.stringify(error, undefined, 2));
      });
  }

}

